export default {
	name:"videocategory",
	data() {
		return {
			category:[]
		}
	},
	methods:{
		getcategory() {
			uni.request({



				

				url:"http://157.122.54.189:9088/videoimg/v1/videowp/category",

				
				success: (result) => {
					//console.log(result);
					this.category =result.data.res.category;
				}
			})
		},
		tocategory(id) {
			uni.navigateTo({
				url:"./videoCategory/videoCategory?id="+id
			})
		}
	},
	mounted() {
		this.getcategory()
	}
}
